.ToDoItem {
  background-color: #AEE2FF;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 4px 16px 8px rgba(32, 35, 41, 0.15);
  border-radius: 8px;
  border: 2px solid ; 
  transition: 0.5s;
}
.ToDoItem:hover {
  transform: translateY(-6px);
  border-radius: 1.2rem 1.2rem 0 0; 
  box-shadow: 7px 14px 0px #000000; 
}
.ToDoItem--completed {
  background-color: #ddd;
  opacity: .8;
  box-shadow: none;
}
.ToDoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
}
.ToDoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 28px;
  font-weight: bold;
}
.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}
.Icon-delete {
  position: absolute;
  right: 12px;
  font-size: 32px;
}
.Icon-delete:hover {
  color: red;
}


@media (max-width: 930px) {
  .ToDoItem--completed {
    opacity: .9;
  }
}